/*------------------------------------*\
    首頁 banner 區塊 */
/*------------------------------------*\ */

.header {
    @include backgroundCover('../images/banner.png');
    @include flexGrid(flex-end);
    text-align: right;
    height: 787px;

    &-container {
        margin: 0 auto;
        padding: 0 12px;
        width: 1320px; }

    @include respond-to('sphone') {
        @include size(100%,396px); }

    &-subtitle {
        @include fontSize(36px, 54px);
        color: $color-third;

        @include respond-to('sphone') {
            @include fontSize(16px,24px); } }

    &-title {
        @include fontSize(64px, 96px, bold);
        color: $color-third;
        @include respond-to('sphone') {
            @include fontSize(24px,36px,bold); } }

    &-btn {
        @include fontSize(20px, 30px);
        background-color: $color-primary;
        color: $color-white;
        padding: 6px 12px;
        cursor: pointer;
        @extend .border-none; } }
