/*------------------------------------*\
    導覽列設定 */
/*------------------------------------*\ */

.navbar {
    background-color: $color-primary;

    // 導覽列整體架構
    &-item {
        @include flexGrid(space-between);
        position: relative;

        @include respond-to('sphone') {
            display: block; } }

    // 導覽列表
    &-menu {
        @include flexGrid;

        // 漢堡選單設定
        @include respond-to('sphone') {
            transition: .3s;
            display: block;
            position: absolute;
            top: 81px;
            left: 0;
            right: 0;
            background-color: $color-primary;
            opacity: 0;

            &.active {
                opacity: 1; } }

        &-item {
            @include flexGrid(space-between);
            margin-right: 64px;
            &:last-child {
                margin-right: 0; }

            @include respond-to('sphone') {
                margin-right: 8px;
                display: block;
                text-align: right;
                &:last-child {
                    margin-right: 8px; } } }

        &-link {
            @include fontSize(20px, 30px);
            color: $color-white;
            transition: .2s;
            display: inline-block;
            padding: 8px 0;
            @extend .link-border-animation; } }

    // LOGO 圖片
    &-img {
        padding: 16px; }

    // 漢堡選單設定
    &-hamburger {

        &-menu {
            display: none;
            cursor: pointer;
            @include size(30px, 30px);
            position: absolute;
            top: 25px;
            right: 20px;

            @include respond-to('sphone') {
                display: inline-block; } }

        &-btn {
            @include size(30px, 3px);
            background-color: $color-white;
            position: absolute;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            transition: .3s;

            &::after,
            &::before {
                content: '';
                @include size(30px, 3px);
                background-color: $color-white;
                position: absolute;
                left: 0;
                transition: .3s; }

            &::before {
                transform: translateY(8px); }
            &::after {
                transform: translateY(-8px); }

            &.active {
                background-color: transparent;

                &::before {
                    transform: translateY(0px) rotate(45deg); }
                &::after {
                    transform: translateY(0px) rotate(-45deg); } } } } }
