/* ------------------- 動畫設定 ------------------- */
.link-border-animation, .navbar-menu-link, .footer-menu-link, .footer-copyright-link {
  position: relative; }
  .link-border-animation::after, .navbar-menu-link::after, .footer-menu-link::after, .footer-copyright-link::after {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 0;
    height: 1px;
    background-color: white;
    transition: .3s; }
  .link-border-animation:hover::after, .navbar-menu-link:hover::after, .footer-menu-link:hover::after, .footer-copyright-link:hover::after {
    width: 100%; }

.hover-translate-animation {
  transition: .3s; }
  .hover-translate-animation:hover {
